.meal {
  display: flex;
  justify-content: space-between;
  margin: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #ccc;
}

.meal h3 {
  margin: 0 0 0.25rem 0;
}

.description {
  font-style: italic;
}

.price {
  margin-top: 0.25rem;
  font-weight: bold;
  color: #ad5502;
  font-size: 1.25rem;
}

.but {
  background-color: #8a2b06;
  border: none;
  color: white;
  padding: 12px 5px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin-right: 30px;
}

.delete {
  background-color: #e46060;
  border: none;
  color: white;
  padding: 12px 5px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  
}